import wbtable from './wbtable.vue';
let table = {};
table.install = function(Vue) {
  Vue.prototype.$myMethod = function(arr) {
    if (arr.length < 0) {
      return false;
    } else {
      arr = arr.join('');
      return arr;
    }
  };
  Vue.component(wbtable.name, wbtable); // testPanel.name name
};
export default table;
